/* stylelint-disable selector-class-pattern */

@mixin header-dropdown {
  --dropdown-value-color: var(--white);
  --dropdown-indicator-color: var(--white);
  --dropdown-focus-color: var(--dark-text-button-pressed-background, #222248);
  --dropdown-menu-width: 165px;

  align-items: center;
  color: var(--dropdown-value-color);
  display: flex;
  font-size: rem(16);
  line-height: line-height(24, 16);

  .dropdown {
    border: none;
    box-shadow: none;
    outline: none;

    :global(.h3-select__menu) {
      min-width: 100%;
      right: 0;
      width: var(--dropdown-menu-width);
    }

    :global(.h3-select__value-container) {
      padding-right: 0;
    }

    :global(.h3-select__control) {
      padding-left: 0;
      padding-right: 5px;
    }
  }
}
